"use client";

import * as React from "react";
import { Loader2 } from "lucide-react";
import axiosClient from "@/lib/axiosClient";
import Preview from "@/app/preview/dummuypreview/page";

interface Props {
  slug: number;
}

export default function LandingPageClient({ slug }: Props) {
  const [isLoading, setIsLoading] = React.useState(true);
  const [payload, setPayload] = React.useState<any>(null);

  React.useEffect(() => {
    if (!slug || isNaN(slug)) {
      setIsLoading(false);
      return;
    }

    const fetchPreviewData = async () => {
      try {
        setIsLoading(true);

        const res = await axiosClient.get(`/products/${slug}/landing-page`);

        if (res.data?.success && res.data?.data?.sections?.length) {
          const activeSections = res.data.data.sections
            .filter((sec: any) => sec.is_published)
            .sort((a: any, b: any) => a.sort_order - b.sort_order);

          setPayload({
            templateName: "modern",
            sections: activeSections.map((sec: any) => ({
              section: sec.global_section.key,
              content: sec.content ?? {},
            })),
            styles: {
              primaryColor: "#ecedf4ff",
              backgroundColor: "#000000",
              textColor: "#a97d38",
              headlineSize: 28,
              paragraphSize: 16,
            },
          });
        } else {
          setPayload({
            templateName: "modern",
            sections: [
              {
                section: "hero",
                content: {
                  title: "No Sections Published",
                  subtitle: "Enable sections in the builder to see them here.",
                },
              },
            ],
            styles: {
              primaryColor: "#ecedf4ff",
              backgroundColor: "#000000",
              textColor: "#a97d38",
              headlineSize: 28,
              paragraphSize: 16,
            },
          });
        }
      } catch (error) {
        console.error("Error fetching landing page data:", error);

        setPayload({
          templateName: "modern",
          sections: [
            {
              section: "hero",
              content: {
                title: "Error Loading Preview",
                subtitle: "Please try again later.",
              },
            },
          ],
          styles: {
            primaryColor: "#ecedf4ff",
            backgroundColor: "#000000",
            textColor: "#a97d38",
            headlineSize: 28,
            paragraphSize: 16,
          },
        });
      } finally {
        setIsLoading(false);
      }
    };

    fetchPreviewData();
  }, [slug]);

  if (isLoading) {
    return (
      <div className="flex h-screen items-center justify-center">
        <Loader2 className="h-10 w-10 animate-spin" />
      </div>
    );
  }

  if (!payload) {
    return <div className="p-8 text-center">No preview data available.</div>;
  }

  return <Preview data={payload} productId={slug} />;
}